.links {
    display: flex;
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 100;
    text-align: right;
}

.link {
    display: inline-block;
    padding: 5px 10px;
    text-decoration: none;
    transition-duration: 0.25s;
    color: #ddd;
    text-transform: uppercase;
    border: none;
    position: relative;
    z-index: 2;
    font-size: 14px;
    letter-spacing: 1px;
}

.link:hover {
    color: #111;
    border-bottom: 1px solid #fff;
}

.link::before {
    content: '';
    height: 0;
    width: 100%;
    position: absolute;
    right: 0;
    bottom: 0;
    z-index: -1;
    background: transparent;
}

.link:hover::before {
    background-color: #fff;
    transition-duration: 0.25s;
    height: 100%;
}

